import React from "react"
import { Link, graphql } from "gatsby"

// import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useRemarkForm } from "gatsby-tinacms-remark"
import { usePlugin, useForm } from "tinacms"
import { InlineForm, InlineText } from "react-tinacms-inline"
import { InlineWysiwyg } from "react-tinacms-editor"

const BlogPostTemplate = ({ data, location }) => {
  // const formConfig = {
  //   id: data.markdownRemark.id,   // a unique identifier for this instance of the form
  //   label: 'Blog Post',             // name of the form to appear in the sidebar
  //   initialValues: data.markdownRemark, // populate the form with starting values
  //   fields: [                    // define fields to appear in the form
  //     {
  //       name: 'frontmatter.title',
  //       label: 'Title',
  //       component: 'text',
  //     },
  //     {
  //       name: 'frontmatter.date',
  //       label: 'Date',
  //       component: 'text',
  //     },
  //     {
  //       name: 'frontmatter.description',
  //       label: 'Description',
  //       component: 'text',
  //     },
  //     {
  //       name: 'rawMarkdownBody',
  //       component: 'markdown',      // `component` accepts a predefined components or a custom React component
  //     },
  //   ]
  // }

  const [post, form] = useRemarkForm(data.markdownRemark)

  usePlugin(form)

  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { previous, next } = data

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <InlineForm form={form}>
        <div className="page-content-padding ">
          <article className="">
            <header>
              <h1>
                <InlineText name="frontmatter.title" />
              </h1>
              <p
                style={{
                  display: `block`,
                }}
              >
                {post.frontmatter.date}
              </p>
              <hr />
            </header>
            <InlineWysiwyg name="rawMarkdownBody">
              <div
                dangerouslySetInnerHTML={{
                  __html: post.html,
                }}
              />
            </InlineWysiwyg>
            <hr style={{}} />
          </article>

          <nav>
            <ul
              style={{
                display: `flex`,
                flexWrap: `wrap`,
                justifyContent: `space-between`,
                listStyle: `none`,
                padding: 0,
              }}
            >
              <li>
                {previous && (
                  <Link to={previous.fields.slug} rel="prev">
                    ← {previous.frontmatter.title}
                  </Link>
                )}
              </li>
              <li>
                {next && (
                  <Link to={next.fields.slug} rel="next">
                    {next.frontmatter.title} →
                  </Link>
                )}
              </li>
            </ul>
          </nav>
        </div>{" "}
      </InlineForm>
    </Layout>
  )
}
const BlogPostForm = {
  fields: [
    {
      label: "Title",
      name: "frontmatter.title",
      description: "Enter the title of the post here",
      component: "text",
    },
    {
      label: "Description",
      name: "frontmatter.description",
      description: "Enter the post description",
      component: "textarea",
    },
  ],
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
      ...TinaRemark
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`

